<template>
  <div class="outer">
    <div>
      <div id="slider-box" ref="box" onselectstart="return false;">
        <div class="slider-bgColor" ref="bgColor"></div>
        <div class="slider-txt" ref="txt"></div>
        <!--i-->
        <div class="slider-slider" ref="slider">
          <!-- <i class="iconfont icon-double-right"></i> -->
        </div>
      </div>
    </div>
    <slot v-if="ifLocked"></slot>
  </div>
</template>

<script>
import setting from "../js/setting";
export default {
  name: "mc-unlock",
  mixins: [setting],
  props: ["value"],
  data: function () {
    return {
      ifLocked: false,
    };
  },
  emits: {
    ["update:value"]: () => {
      return true;
    },
  },

  mounted: function () {
    /*
     * @Descripttion:
     * @version:
     * @Author: sueRimn
     * @Date: 2020-05-10 21:57:47
     * @LastEditors: sueRimn
     * @LastEditTime: 2020-05-10 21:58:29
     */
    var $this = this;
    //
    function getEle(selector) {
      return $this.$refs[selector];
      //   return document.querySelector(selector);
    }
    //DOM
    var box = getEle("box"), //
      bgColor = getEle("bgColor"), //
      txt = getEle("txt"), //
      slider = getEle("slider"), //
      //   icon = getEle(".slider-slider>i"),
      successMoveDistance = box.offsetWidth - slider.offsetWidth, //
      downX, //
      isSuccess = false; //

    //
    slider.onmousedown = mousedownHandler;

    //3.1
    function mousedownHandler(e) {
      bgColor.style.transition = "";
      slider.style.transition = "";
      e = e || window.event || e.which;
      downX = e.clientX;
      //
      document.onmousemove = mousemoveHandler;
      document.onmouseup = mouseupHandler;
    }

    //
    function getOffsetX(offset, min, max) {
      if (offset < min) {
        offset = min;
      } else if (offset > max) {
        offset = max;
      }
      return offset;
    }

    //3.1.1
    function mousemoveHandler(e) {
      if (!isSuccess) {
        e = e || window.event || e.which;
        var moveX = e.clientX;
        var offsetX = getOffsetX(moveX - downX, 0, successMoveDistance);
        bgColor.style.width = offsetX + "px";
        slider.style.left = offsetX + "px";

        if (offsetX == successMoveDistance) {
          success();
        }
      }

      //
      e.preventDefault();
    }

    //3.1.2
    function mouseupHandler() {
      if (!isSuccess) {
        bgColor.style.width = 0 + "px";
        slider.style.left = 0 + "px";
        bgColor.style.transition = "width 0.8s linear";
        slider.style.transition = "left 0.8s linear";
      }
      document.onmousemove = null;
      document.onmouseup = null;
    }

    //
    function success() {
      isSuccess = true;
      txt.innerHTML = "";
      bgColor.style.width = "100%";
      bgColor.style.backgroundColor = "lightgreen";
      slider.className = "slider active";
      $this.$emit("update:value", true);
      //   icon.className = "iconfont icon-xuanzhong";
      //
      $this.ifLocked = true;
      slider.onmousedown = null;
      document.onmousemove = null;
    }
  },
};
</script>
<style scoped>
.outer {
  width: 100%;
  display: inline-block;
  text-align: center;
}

#slider-box {
  position: relative;
  width: 100%;
  height: 40px;
  margin: 10px auto;
  margin-top: 10px;
  background-color: #e8e8e8;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
}

.slider-bgColor {
  position: absolute;
  left: 0;
  top: 0;
  width: 0;
  height: 100%;
  background-color: lightblue;
}
.slider-txt {
  position: absolute;
  width: 100%;
  height: 40px;
  line-height: 40px;
  font-size: 14px;
  color: #000;
  text-align: center;
}
.slider-slider {
  position: absolute;
  left: 0;
  top: 0;
  width: 50px;
  height: 38px;
  border: 1px solid #ccc;
  background: #fff;
  text-align: center;
  cursor: move;
}
.slider-slider > i {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.slider-slider.active > i {
  right: 0;
  color: green;
}
</style>